@import url(https://fonts.googleapis.com/css?family=Petit+Formal+Script);
@import url(https://fonts.googleapis.com/css?family=Slabo+27px);
@import url(https://fonts.googleapis.com/css?family=Roboto);
:root {
  --frontOrange: #fdbb2d;
  --userDarkBlue: #1a2a6c;
}

.flex-container {
  display: flex;
}

.grid-wrapper {
  display: grid;
  grid-template-areas: 'header header'
                       'main main'
                       'footer footer';
  background-color: #fdbb2d;
  background-color: var(--frontOrange);
  background: -webkit-linear-gradient(
                      rgba(0, 0, 0, 0.1),
                      rgba(0, 0, 0, 0.1)
                    ),
                    url(/static/media/back.1bc16fd7.png) no-repeat center center fixed;
  background: linear-gradient(
                      rgba(0, 0, 0, 0.1),
                      rgba(0, 0, 0, 0.1)
                    ),
                    url(/static/media/back.1bc16fd7.png) no-repeat center center fixed;
  background-size: cover;
  grid-template-rows: 2fr 4fr 0.5fr;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
}

h1, h2 {
  color: white;
  text-shadow: 1.5px 1.5px 0px black;
  text-align: center;
  font-family: 'Slabo 27px', sans-serif;
}

p {
  color: white;
  text-shadow: 1.5px 1.5px 0px black;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  /* text-align: center; */
}

/* ======================================================== */

.header-front {
  grid-area: header;

  justify-content: center;
  /* padding-bottom: 2rem; */
  /* align-items: center; */
  /* height: 30vh; */
}

.header-front h1 {
  font-size: 5rem;
  margin-top: 1rem;
  font-family: 'Petit Formal Script', cursive;
}

/* ======================================================== */

.main-front {
  /* background-color: var(--frontOrange);
  background-image: linear-gradient(to bottom, var(--frontOrange), #b21f1f, var(--userDarkBlue)); */
  grid-area: main;
  display: flex;
  justify-content: center;
  -webkit-perspective: 600px;
          perspective: 600px;
  /* align-items: center; */
}

.main-front img {
  position: absolute;
  height: 18rem;
  width: auto;
  margin-top: 1rem;
  
}

.spinning {
  -webkit-animation: spin 0.5s linear infinite;
          animation: spin 0.5s linear infinite;
  /* animation: spin 5s linear infinite; */
}

.flipper {
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.card {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.cardFront {
  -webkit-transform: rotateY( 180deg );
          transform: rotateY( 180deg );
  cursor: pointer;
  /* border: 1px solid #021a40; */
}

.is-flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

/* ======================================================== */
/* This pattern is downloaded from www.subtlepatterns.com  */
/* If you need more, that's where to get'em. */
/* ======================================================== */
.footer {
  grid-area: footer;
  /* background: black url(../images/binding_dark.png) repeat 0 0; */
}

.footer a {
  margin: auto;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.footer a:hover {
  /* transform: scale(1.1);  */
}

/* ======================================================== */

.error {
  background: -webkit-linear-gradient(
                      rgba(0, 0, 0, 0.1),
                      rgba(0, 0, 0, 0.1)
                    ),
                    url(/static/media/error.fd0a7a21.png) no-repeat center center fixed;
  background: linear-gradient(
                      rgba(0, 0, 0, 0.1),
                      rgba(0, 0, 0, 0.1)
                    ),
                    url(/static/media/error.fd0a7a21.png) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
}

/* ======================================================== */

@media screen and (max-width: 500px), (min-width: 1400px) {
  .header-front h1 {
    font-size: 8vw;
  }
}

@media screen and (max-width: 500px){
  
  .header-front {
    background-position: 80% 80%;
  }

  .error {
    background: -webkit-linear-gradient(
                        rgba(0, 0, 0, 0.1),
                        rgba(0, 0, 0, 0.1)
                      ),
                      url(data:image/jpeg;base64,) no-repeat center center fixed;
    background: linear-gradient(
                        rgba(0, 0, 0, 0.1),
                        rgba(0, 0, 0, 0.1)
                      ),
                      url(data:image/jpeg;base64,) no-repeat center center fixed;
    background-size: contain;
    height: 100vh;
  }

  .grid-wrapper {
    display: grid;
    grid-template-areas: 'header'
                         'main'
                         'footer';
    grid-template-rows: 1fr 2fr 0.11fr;
    grid-template-columns: 1fr;
  }

  .navbar h1 {
    visibility: hidden;
  }

  .navbar-search {
    margin-top: -7vh;
    margin-left: auto;
    margin-right: auto;
  }

  .navbar-search input{
    font-size: 1rem;
    width: 75vw;
  }

  .main-front {
    grid-template-areas: 'title'
                        'first-middle'
                        'second-middle'
                        'bottom'
                        'search';
    grid-template-rows: 0.4fr 1fr 1fr 0.4fr 0.4fr;
    grid-template-columns: 1fr;
  } 
  .main-first-middle, .main-second-middle {
    max-width: 90vw;
    text-align: center;
  }

  .main-front h1 {
    font-size: 8vw;
  }
  .main-front h2 {
    font-size: 6vw;
  }
  .main-front p {
    font-size: 4vw;
  }

}
@-webkit-keyframes modal-video {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes modal-video {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px); }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

@keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px); }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

.modal-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000000;
  cursor: pointer;
  opacity: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-name: modal-video;
          animation-name: modal-video;
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out; }

.modal-video-effect-exit {
  opacity: 0; }
  .modal-video-effect-exit .modal-video-movie-wrap {
    -webkit-transform: translate(0, 100px);
    transform: translate(0, 100px); }

.modal-video-body {
  max-width: 940px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: table; }

.modal-video-inner {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%; }

.modal-video-movie-wrap {
  width: 100%;
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  background-color: #333;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-name: modal-video-inner;
          animation-name: modal-video-inner;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; }
  .modal-video-movie-wrap iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.modal-video-close-btn {
  position: absolute;
  z-index: 2;
  top: -35px;
  right: -35px;
  display: inline-block;
  width: 35px;
  height: 35px;
  overflow: hidden;
  border: none;
  background: transparent; }
  .modal-video-close-btn:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .modal-video-close-btn:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .modal-video-close-btn:before, .modal-video-close-btn:after {
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #fff;
    border-radius: 5px;
    margin-top: -6px; }

